@import url("https://fonts.googleapis.com/css?family=Rajdhani:400,500,600,700&display=swap");

/* Основные стили */
body {
  font-family: 'Rajdhani', sans-serif;
  background-color: #161b28;
  color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Заголовки */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Rajdhani', sans-serif;
  color: #fff;
  margin: 0;
}

/* Параграфы */
p {
  font-family: 'Rajdhani', sans-serif;
  color: #fff;
  margin: 0;
}

/* Ссылки */
a {
  font-family: 'Rajdhani', sans-serif;
  color: #4ff461;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #fff;
}

/* Общие настройки для всех элементов */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}